import { FC } from 'react';
import { useLocation } from 'react-router-dom';
import { Layout, Paragraph, Section, Title } from 'components';
import css from './ErrorPage.module.scss';

interface ILocationState {
  errorText: string;
}

const ErrorPage: FC = () => {
  const { state } = useLocation();
  const { errorText } = state as ILocationState;

  return (
    <Layout>
      <Section className={css.errorPage} padding={true}>
        <Title Tag="h1" className="page-title">
          <span>{'<'}</span>
          Something went wrong!
          <span>{'/>'}</span>
        </Title>

        <Paragraph className={css.errorPage_text}>{errorText || 'Unknown error!'}</Paragraph>
      </Section>
    </Layout>
  );
};

export default ErrorPage;
