const roundedSquareWave = (t: number, delta: number, a: number, f: number) => {
  return ((2 * a) / Math.PI) * Math.atan(Math.sin(2 * Math.PI * t * f) / delta);
};

const COUNT_DOTS = 1000;
const MAX_SIZE_VIEW = 20;
const HALF_MAX_SIZE_VIEW = MAX_SIZE_VIEW / 2;
const DOT_SIZE = 0.15;

function Dots() {
  const ref = useRef<THREE.InstancedMesh>(null!);

  useLayoutEffect(() => {
    if (!ref.current) return;

    const transform = new THREE.Matrix4();
    let angle = 0;
    let countInRound = 6;

    transform.setPosition(0, 0, 0);
    ref.current.setMatrixAt(0, transform);

    for (let i = 1; i < COUNT_DOTS; i++) {
      const sinusResult = Math.sin(angle) * (MAX_SIZE_VIEW - (i % MAX_SIZE_VIEW));
      const cosinusResult = Math.cos(angle) * (MAX_SIZE_VIEW - (i % MAX_SIZE_VIEW));
      // const sinusResult = Math.sin(angle) * DOT_SIZE * countInRound;
      // const cosinusResult = Math.cos(angle) * DOT_SIZE * countInRound;

      if (i % countInRound === 0) {
        countInRound *= 2;
        angle = 360 / countInRound;
      } else {
        angle += 360 / countInRound;
      }

      const x = sinusResult;
      const y = cosinusResult;
      transform.setPosition(x, y, 0);
      ref.current.setMatrixAt(i, transform);
    }
    console.log(ref.current);
  }, []);

  // const { vec, transform, positions, distances } = useMemo(() => {
  //   const vec = new THREE.Vector3();
  //   const transform = new THREE.Matrix4();

  //   const positions = [...Array(COUNT_DOTS)].map((_, i) => {
  //     const position = new THREE.Vector3();
  //     position.x = (i % MAX_SIZE_VIEW) - HALF_MAX_SIZE_VIEW;
  //     position.y = HALF_MAX_SIZE_VIEW - Math.floor(i / HALF_MAX_SIZE_VIEW);
  //     position.y += (i % 2) * Math.random();
  //     position.x += Math.random() * 0.3;
  //     position.y += Math.random() * 0.3;
  //     return position;
  //   });
  //   const right = new THREE.Vector3(1, 0, 0);
  //   const distances = positions.map((pos) => pos.length() + Math.cos(pos.angleTo(right) * 8) * 0.5);

  //   return { vec, transform, positions, distances };
  // }, []);

  // useFrame(() => {
  //   if (!ref.current) return;

  //   for (let i = 0; i < COUNT_DOTS; i++) {
  //   }

  //   ref.current.instanceMatrix.needsUpdate = true;
  // });

  return (
    <instancedMesh ref={ref} args={[null!, null!, COUNT_DOTS]}>
      <circleGeometry args={[DOT_SIZE]} />
      <meshBasicMaterial color={'#ffffff'} />
    </instancedMesh>
  );
}

const AppBackground = () => {
  return (
    <div className={css.appBackground}>
      <Canvas camera={{ position: [0, 0, MAX_SIZE_VIEW], fov: 75 }}>
        {/* <color attach="background" fromArray={['blue', 'red']} /> */}
        <Dots />
      </Canvas>
    </div>
  );
};

