import { FC } from 'react';
import OneSkillBox from './OneSkillBox';
import { SKILLS_PHOTO_ITEMS } from 'settings';

const SkillBoxes: FC = () => {
  if (SKILLS_PHOTO_ITEMS.length === 0) return null;
  return (
    <>
      {SKILLS_PHOTO_ITEMS.map((itemData, index) => (
        <OneSkillBox key={itemData.id} {...itemData} index={index} />
      ))}
    </>
  );
};

export default SkillBoxes;
