import { createHashRouter } from 'react-router-dom';
import Home from 'pages/Home';
import EducationPage from 'pages/EducationPage';
import ExperiencePage from 'pages/ExperiencePage';
import SkillsPage from 'pages/SkillsPage';
import ProjectsPage from 'pages/ProjectsPage';
import OneProject from 'pages/OneProject';
import OneCertificate from 'pages/OneCertificate';
import ErrorBoundaryRoute from '../ErrorBoundaryRoute';
import ErrorPage from 'pages/ErrorPage';
import NotFoundPage from 'pages/NotFoundPage';
import { PAGES_LINKS } from 'settings';

export const router = createHashRouter([
  {
    path: PAGES_LINKS.home,
    element: <Home />,
    errorElement: <ErrorBoundaryRoute />,
  },
  {
    path: PAGES_LINKS.education,
    element: <EducationPage />,
    errorElement: <ErrorBoundaryRoute />,
  },
  {
    path: PAGES_LINKS.certificate,
    element: <OneCertificate />,
    errorElement: <ErrorBoundaryRoute />,
  },
  {
    path: PAGES_LINKS.experience,
    element: <ExperiencePage />,
    errorElement: <ErrorBoundaryRoute />,
  },
  {
    path: PAGES_LINKS.skills,
    element: <SkillsPage />,
    errorElement: <ErrorBoundaryRoute />,
  },
  {
    path: PAGES_LINKS.projects,
    element: <ProjectsPage />,
    errorElement: <ErrorBoundaryRoute />,
  },
  {
    path: PAGES_LINKS.oneProject,
    element: <OneProject />,
    errorElement: <ErrorBoundaryRoute />,
  },
  {
    path: PAGES_LINKS.errorPage,
    element: <ErrorPage />,
    errorElement: <ErrorBoundaryRoute />,
  },
  {
    path: PAGES_LINKS.notFoundRoute,
    element: <NotFoundPage />,
    errorElement: <ErrorBoundaryRoute />,
  },
]);
