import { FC } from 'react';
import { ICommonComponent } from 'types';

interface ITitle extends ICommonComponent {
  Tag?: 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6';
}

const Title: FC<ITitle> = ({ Tag = 'h1', children, className = 'title' }) => {
  return <Tag className={className}>{children}</Tag>;
};

export default Title;
