import { FC } from 'react';
import LanguagesListItem from './LanguagesListItem';
import { ILanguageItem } from 'types';
import css from './LanguagesList.module.scss';

const LanguagesList: FC<{ listSkillsData: Array<ILanguageItem> }> = ({ listSkillsData }) => {
  if (listSkillsData.length === 0) return null;

  return (
    <>
      <ul className={css.languagesList}>
        {listSkillsData.map((courseItemData) => (
          <LanguagesListItem key={courseItemData.id} {...courseItemData} />
        ))}
      </ul>
    </>
  );
};

export default LanguagesList;
