import { FC } from 'react';
import classnames from 'classnames';
import { IContactsListItem } from 'types';
import css from './ContactsList.module.scss';

const ContactsListItem: FC<IContactsListItem> = ({ link, text, ariaLabel, className }) => {
  return (
    <li className={css.contactsList__item}>
      <a
        className={classnames(css.contactsList__link, {
          [className || '']: className,
        })}
        href={link}
        aria-label={ariaLabel}
      >
        {text}
      </a>
    </li>
  );
};

export default ContactsListItem;
