import { FC, ReactNode } from 'react';
import { getTimeForAttribute } from 'utils';

export interface ITime {
  month?: string;
  year: string;
  className?: string;
  children: ReactNode;
}

const Time: FC<ITime> = ({ month, year, className = '', children }) => {
  if (isNaN(Number(year))) {
    return <p className={className}>{year}</p>;
  }

  return (
    <time className={className} dateTime={getTimeForAttribute(year, month)}>
      {children}
    </time>
  );
};

export default Time;
