import { useRef } from 'react';
import { extend, Object3DNode, useFrame, useLoader } from '@react-three/fiber';
import { GradientTexture, MeshWobbleMaterial } from '@react-three/drei';
import THREE, { Group, TextureLoader } from 'three';
import { AXIOS_Z_POSITION_PHOTO } from 'settings';
import photo from '../../../assets/images/my_photo.jpg';

const PhotoBox = () => {
  const SIZE_PHOTO = 3.8;
  const ref = useRef<Group>(null);
  const colorMap = useLoader(TextureLoader, photo);

  useFrame((state) => {
    if (!ref.current) {
      return;
    }

    const t = state.clock.getElapsedTime();
    ref.current.rotation.x = Math.sin(t / 2) / 16;
    ref.current.rotation.y = Math.cos(t / 2) / 8;
  });

  return (
    <group ref={ref}>
      {/* <mesh position={[0, 0, AXIOS_Z_POSITION_PHOTO - 1]}>
        <tubeGeometry args={[0, 20, 2, 8, false]} />
        <meshPhongMaterial>
          <GradientTexture stops={[0, 1]} colors={['#010101', '#2e2e2e']} />
        </meshPhongMaterial>
      </mesh> */}

      <mesh position={[0, 0, AXIOS_Z_POSITION_PHOTO]}>
        <circleGeometry args={[SIZE_PHOTO, 64, 64]} />
        <MeshWobbleMaterial map={colorMap} speed={1.8} factor={0.3} />
      </mesh>
    </group>
  );
};

export default PhotoBox;
