import { FC } from 'react';
import { Button } from 'components';
import css from './PaginationItem.module.scss';

interface IPaginationItemProps {
  currentPage: number;
  number: number;
  content: string | number;
  stopClick: boolean;
  clickPaginationButton: (id: number) => void;
}

const PaginationItem: FC<IPaginationItemProps> = ({
  currentPage,
  content,
  number,
  stopClick,
  clickPaginationButton,
}: IPaginationItemProps) => {
  return (
    <li className={css.paginationList__item}>
      {content === currentPage ? (
        <span className={css.paginationList__item_current}>{content.toString()}</span>
      ) : (
        <Button
          className={css.paginationList__item_button}
          disabled={stopClick}
          onClick={() => clickPaginationButton(number)}
        >
          {content.toString()}
        </Button>
      )}
    </li>
  );
};

export default PaginationItem;
