import { FC } from 'react';
import classnames from 'classnames';
import Time from '../Time/Time';
import css from './TimeInterval.module.scss';

interface ITimeInterval {
  startMonth?: string;
  startYear: string;
  endMonth?: string;
  endYear: string;
  className?: string;
}
const TimeInterval: FC<ITimeInterval> = ({
  startMonth,
  startYear,
  endMonth,
  endYear,
  className,
}) => {
  return (
    <div
      className={classnames(css.interval, {
        [className || '']: className,
      })}
    >
      <Time month={startMonth} year={startYear} className={css.interval__start}>
        {startMonth && <span>{startMonth}.</span>}
        <span className={css.year}>{startYear}</span>
      </Time>

      <Time month={endMonth} year={endYear} className={css.interval__end}>
        {endMonth && <span>{endMonth}.</span>}
        <span className={css.year}>{endYear}</span>
      </Time>
    </div>
  );
};

export default TimeInterval;
