import { IExperienceItem } from 'types';
import css from './ExperienceList.module.scss';

const ExperienceListItem: FC<IExperienceItem> = ({
  startMonth,
  startYear,
  endMonth,
  endYear,
  place,
  position,
  responsibilities,
}) => {
  return (
    <li className={css.experienceList__item}>
      <TimeInterval
        className={css.experienceList__item_date}
        startMonth={startMonth}
        startYear={startYear}
        endMonth={endMonth}
        endYear={endYear}
      />

      <div className={css.experienceList__item_info}>
        <p className={css.item__place}>{place}.</p>
        <p className={css.item__position}>{position}.</p>
      </div>

      <ExperienceListItemResponsibilities responsibilities={responsibilities} />
    </li>
  );
};

export default ExperienceListItem;
