import { FC } from 'react';
import classnames from 'classnames';
import AdvantagesListItem from './AdvantagesListItem';
import { IAdvantagesItem } from 'types';
import css from './AdvantagesList.module.scss';

interface IAdvantagesList {
  className?: string;
  listAdvantages: Array<IAdvantagesItem>;
}
const AdvantagesList: FC<IAdvantagesList> = ({ listAdvantages, className }) => {
  if (listAdvantages.length === 0) return null;

  return (
    <ul
      className={classnames(css.advantagesList, {
        [className || '']: className,
      })}
    >
      {listAdvantages.map((advantagesItemData) => (
        <AdvantagesListItem key={advantagesItemData.id} {...advantagesItemData} />
      ))}
    </ul>
  );
};

export default AdvantagesList;
