import { FC } from 'react';
import classnames from 'classnames';
import { NavLink } from 'react-router-dom';
import { HEADER_NAV } from 'settings';
import css from './Header.module.scss';
import { TSimpleFunction } from 'types';

const HeaderNavigation: FC<{ isShowMenu: boolean; hideMenu: TSimpleFunction }> = ({
  isShowMenu,
  hideMenu,
}) => {
  if (HEADER_NAV.length === 0) return null;

  const hideMenuHandler = (event: React.MouseEvent) => {
    event.stopPropagation();

    hideMenu();
  };

  return (
    <nav
      className={classnames(css.navigation, {
        [css.active_menu]: isShowMenu,
      })}
      aria-label="Main navigation."
      onClick={hideMenuHandler}
    >
      <ul className={css.navigation__list}>
        {HEADER_NAV.map((navItem) => (
          <li className={css.navigation__list_item} key={navItem.id}>
            <NavLink
              onClick={hideMenu}
              to={navItem.link}
              className={({ isActive }) =>
                classnames(css.navigation__list_link, {
                  [css.active]: !!isActive,
                })
              }
            >
              {navItem.text}
            </NavLink>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default HeaderNavigation;
