/*  get correct data for attribute time tag start  */
export const getTimeForAttribute = (year: string, month?: string) => {
  if (!month) return year;

  return `${year}-${month}`;
};
/*  get correct data for attribute time tag end  */

/*  check more or less value start */
type TCheckValue = (value: number, comparisonValue: number, varient: 'more' | 'less') => boolean;

export const checkValue: TCheckValue = (
  value: number,
  comparisonValue: number,
  varient: 'more' | 'less' = 'more'
): boolean => {
  if (varient === 'more') {
    return value >= comparisonValue;
  }

  return value <= comparisonValue;
};
/*  check more or less value end */
