import { FC } from 'react';
import classnames from 'classnames';
import { Title } from 'components';
import CoursesListItem from './CoursesListItem';
import { ICoursesItem } from 'types';
import css from './CoursesList.module.scss';

const CoursesList: FC<{ listCoursesData: Array<ICoursesItem> }> = ({ listCoursesData }) => {
  if (listCoursesData.length === 0) return null;

  return (
    <>
      <Title Tag="h2" className="title">
        <span>{'<'}</span>
        Courses
        <span>{'/>'}</span>
      </Title>

      <ol className={classnames(css.coursesList, 'grid-list')}>
        {listCoursesData.map((courseItemData) => (
          <CoursesListItem key={courseItemData.id} {...courseItemData} />
        ))}
      </ol>
    </>
  );
};

export default CoursesList;
