import { FC } from 'react';
import classnames from 'classnames';
import { Time } from 'components';
import { ICoursesItem } from 'types';
import css from './CoursesList.module.scss';
import { Link } from 'react-router-dom';
import { PAGES_LINKS } from 'settings';

const CoursesListItem: FC<ICoursesItem> = ({
  path,
  image,
  endYear,
  endMonth,
  nameCourse,
  place,
}) => {
  return (
    <li className={classnames(css.coursesList__item, 'grid-list__item grid-list__link')}>
      <img
        className={classnames(css.coursesList__item_certificate, 'grid-list__link_img')}
        src={process.env.PUBLIC_URL + '/' + image}
        alt={`Certificate ${nameCourse}.`}
        decoding="async"
        loading="lazy"
      />

      <Link
        to={PAGES_LINKS.education + '/' + path}
        className={classnames(css.coursesList__item_content, 'grid-list__link_description')}
      >
        <Time year={endYear} month={endMonth}>
          {endMonth && endMonth + '.'}
          {endYear}
        </Time>

        <span className={css.separator}>{'-'}</span>

        <span>{place}</span>

        <span className={css.name}>{nameCourse}</span>
      </Link>
    </li>
  );
};

export default CoursesListItem;
