import { FC, useEffect, useState } from 'react';
import usePaginationStore from 'store';
import { Layout, Pagination, Section, Spinner, Title } from 'components';
import ProjectsList from './ProjectsList';
import { COUNT_WORK_PER_PAGE, WORKS_DATA } from 'settings';
import { IWorkItemData } from 'types';

const maxPage = Math.ceil(WORKS_DATA.length / COUNT_WORK_PER_PAGE);

const ProjectsPage: FC = () => {
  const [works, setWorks] = useState<Array<IWorkItemData>>([]);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const currentPage = usePaginationStore((state) => state.currentPage);
  const changeACurrentPage = usePaginationStore((state) => state.changeACurrentPage);

  useEffect(() => {
    setIsLoading(true);

    setTimeout(() => {
      const sliceStartPosition = COUNT_WORK_PER_PAGE * (currentPage - 1);
      const sliceEndPosition = COUNT_WORK_PER_PAGE * (currentPage - 1) + COUNT_WORK_PER_PAGE;

      const worksFromArray: Array<IWorkItemData> = WORKS_DATA.slice(
        sliceStartPosition,
        sliceEndPosition > 0 ? sliceEndPosition : COUNT_WORK_PER_PAGE
      );

      setWorks(worksFromArray);
      setIsLoading(false);
    }, 1000);
  }, [currentPage]);

  return (
    <Layout>
      <Section padding={true}>
        <Title className="title" Tag="h1">
          <span>{'<'}</span>
          Some of my works
          <span>{'/>'}</span>
        </Title>

        {isLoading ? <Spinner /> : <ProjectsList worksData={works} />}

        <Pagination
          maxPage={maxPage}
          currentPage={currentPage}
          stopClick={isLoading}
          changePage={changeACurrentPage}
        />
      </Section>
    </Layout>
  );
};

export default ProjectsPage;
