import { FC, ReactNode } from 'react';
import classnames from 'classnames';
import Wrapper from '../Wrapper';
import { ICommonComponent } from 'types';
import css from './Section.module.scss';

interface ISection extends ICommonComponent {
  classNameWrapper?: string;
  padding?: boolean;
  insertBeforeWrapper?: ReactNode | string;
}

const Section: FC<ISection & React.HTMLAttributes<HTMLTableSectionElement>> = ({
  className,
  classNameWrapper,
  children,
  padding = false,
  insertBeforeWrapper = null,
  ...last
}) => {
  return (
    <section
      className={classnames({
        [className || '']: className,
        [css.padding]: padding,
      })}
      {...last}
    >
      {insertBeforeWrapper}
      <Wrapper className={classNameWrapper}>{children}</Wrapper>
    </section>
  );
};

export default Section;
