import { useRouteError, Navigate } from 'react-router-dom';
import { PAGES_LINKS } from '../../settings';

interface IError {
  message: string;
}

function ErrorBoundaryRoute() {
  const { message = '-   !' } = useRouteError() as IError;

  return <Navigate to={PAGES_LINKS.errorPage} replace state={{ errorText: message }} />;
}

export default ErrorBoundaryRoute;
