import { FC } from 'react';
import { IAdvantagesItem } from 'types';
import css from './AdvantagesList.module.scss';

const AdvantagesListItem: FC<IAdvantagesItem> = ({ value, text }) => {
  return (
    <li className={css.advantagesList__item}>
      <p className={css.advantagesList__item_value}>{value}</p>
      <p className={css.advantagesList__item_text} dangerouslySetInnerHTML={{ __html: text }} />
    </li>
  );
};

export default AdvantagesListItem;
