import { FC } from 'react';
import SkillsListItem from './SkillsListItem';
import { ISkillItem } from 'types';
import css from './SkillsList.module.scss';

const SkillsList: FC<{ listSkillsData: Array<ISkillItem> }> = ({ listSkillsData }) => {
  if (listSkillsData.length === 0) return null;

  return (
    <>
      <ul className={css.skillsList}>
        {listSkillsData.map((courseItemData) => (
          <SkillsListItem key={courseItemData.id} {...courseItemData} />
        ))}
      </ul>
    </>
  );
};

export default SkillsList;
