import { FC } from 'react';
import ExperienceListItem from './ExperienceListItem';
import { IExperienceItem } from 'types';
import css from './ExperienceList.module.scss';

const ExperienceList: FC<{ listExperienceData: Array<IExperienceItem> }> = ({
  listExperienceData,
}) => {
  if (listExperienceData.length === 0) return null;

  return (
    <ol className={css.experienceList}>
      {listExperienceData.map((experienceItemData) => (
        <ExperienceListItem key={experienceItemData.id} {...experienceItemData} />
      ))}
    </ol>
  );
};

export default ExperienceList;
