import { useState } from 'react';
import classnames from 'classnames';
import { Link } from 'react-router-dom';
import { Wrapper, Button } from 'components';
import HeaderNavigation from './HeaderNavigation';
import { PAGES_LINKS } from 'settings';
import css from './Header.module.scss';

const Header = () => {
  const [isShowMenu, setIsShowMenu] = useState<boolean>(false);

  const changeVisibleMenu = () => {
    setIsShowMenu((prev) => !prev);
  };

  const hideMenu = () => {
    setIsShowMenu(false);
  };

  return (
    <header className={css.header}>
      <Wrapper className={css.header__wrapper}>
        <Link to={PAGES_LINKS.home} className={css.header__logo}>
          <span className={css.header__logo_color}>{'<'}</span>
          <span>T</span>
          <span className={css.header__logo_color}>D</span>
          <span>{'/'}</span>
          <span className={css.header__logo_color}>{'>'}</span>
        </Link>

        <HeaderNavigation isShowMenu={isShowMenu} hideMenu={hideMenu} />

        <Button
          className={classnames(css.burger, {
            [css.active_burger]: isShowMenu,
          })}
          onClick={changeVisibleMenu}
          aria-label="Show/hide main menu."
        >
          <span></span>
        </Button>
      </Wrapper>
    </header>
  );
};

export default Header;
