import { FC } from 'react';
import classnames from 'classnames';
import { Link } from 'react-router-dom';
import { PAGES_LINKS } from 'settings';
import { IWorkItemData } from 'types';
import css from './ProjectsList.module.scss';

const ProjectsListItem: FC<IWorkItemData> = ({ id, name, thumbnail }) => {
  return (
    <li className={classnames(css.projectsList__item, 'grid-list__item')}>
      <Link
        className={classnames(css.projectsList__link, 'grid-list__link')}
        to={`${PAGES_LINKS.projects}/${id}`}
      >
        <img
          className={classnames(css.projectsList__link_img, 'grid-list__link_img')}
          src={process.env.PUBLIC_URL + '/' + thumbnail}
          alt={name}
          loading="lazy"
          decoding="async"
        />
        <span className={classnames(css.projectsList__link_name, 'grid-list__link_description')}>
          {name}
        </span>
      </Link>
    </li>
  );
};

export default ProjectsListItem;
