import { FC } from 'react';
import classnames from 'classnames';
import ContactsListItem from './ContactsListItem';
import { IContactsListItem } from 'types';
import css from './ContactsList.module.scss';

interface IContactsList {
  className?: string;
  listContacts: Array<IContactsListItem>;
}

const ContactsList: FC<IContactsList> = ({ className, listContacts }) => {
  if (listContacts.length === 0) return null;

  return (
    <ul
      className={classnames(css.contactsList, {
        [className || '']: className,
      })}
    >
      {listContacts.map((oneContactData) => (
        <ContactsListItem key={oneContactData.id} {...oneContactData} />
      ))}
    </ul>
  );
};

export default ContactsList;
