import { FC } from 'react';
import classnames from 'classnames';
import { ISocialListItem } from 'types';
import css from './SocialList.module.scss';

const SocialListItem: FC<ISocialListItem> = ({ link, className, title }) => {
  return (
    <li className={css.socialList__item}>
      <a
        href={link}
        className={classnames(css.socialList__link, {
          [className || '']: className,
        })}
        target="_blank"
        rel="noreferrer"
        title={title}
      ></a>
    </li>
  );
};

export default SocialListItem;
