import { FC } from 'react';
import css from './ExperienceList.module.scss';

const ExperienceListItemResponsibilities: FC<{ responsibilities: Array<string> | null }> = ({
  responsibilities,
}) => {
  if (!responsibilities || responsibilities.length === 0) return null;
  return (
    <>
      <p className={css.responsibilities__title}>Responsibilities:</p>

      <ul className={css.responsibilitiesList}>
        {responsibilities.map((item) => (
          <li className={css.responsibilitiesList__item} key={item}>
            {item}
          </li>
        ))}
      </ul>
    </>
  );
};

export default ExperienceListItemResponsibilities;
