import { FC } from 'react';
import classnames from 'classnames';
import SocialListItem from './SocialListItem';
import { ISocialListItem } from 'types';
import css from './SocialList.module.scss';

interface ISocialList {
  className?: string;
  listSocialData: Array<ISocialListItem>;
}
const SocialList: FC<ISocialList> = ({ listSocialData, className }) => {
  if (listSocialData.length === 0) return null;

  return (
    <ul
      className={classnames(css.socialList, {
        [className || '']: className,
      })}
    >
      {listSocialData.map((socialItemData) => (
        <SocialListItem key={socialItemData.id} {...socialItemData} />
      ))}
    </ul>
  );
};

export default SocialList;
