import { FC, useEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { Layout, Paragraph, Section, Title } from 'components';
import { PAGES_LINKS, COURSES_DATA } from 'settings';
import { ICoursesItem } from 'types';
import css from './OneCertificate.module.scss';

const OneCertificate: FC = () => {
  const [workInfo, setWorkInfo] = useState<ICoursesItem | null>(null);
  const navigate = useNavigate();
  const { id: idLink } = useParams();

  useEffect(() => {
    if (!idLink) {
      navigate(PAGES_LINKS.notFoundPage);
      return;
    }

    const productData = COURSES_DATA.filter(({ path }) => path === idLink)[0];

    if (!productData) {
      navigate(PAGES_LINKS.notFoundPage);
      return;
    }

    setWorkInfo(productData);
  }, [idLink, navigate]);

  if (!idLink || !workInfo) return null;

  const { image, nameCourse, place, endYear } = workInfo;
  return (
    <Layout>
      <Section padding={true}>
        <Title className="page-title" Tag="h1">
          <span>{'<'}</span>
          {nameCourse}
          <span>{'/>'}</span>
        </Title>

        <img
          className={css.certificate__image}
          src={process.env.PUBLIC_URL + '/' + image}
          alt={nameCourse}
        />

        {place && (
          <Paragraph className={css.certificate__text}>
            {endYear && endYear + ' - '}
            {place}
          </Paragraph>
        )}

        <div className={css.certificate__buttons}>
          <Link className={css.certificate__buttons_backButton} to={PAGES_LINKS.education}>
            Back
          </Link>
        </div>
      </Section>
    </Layout>
  );
};

export default OneCertificate;
