import { FC, useEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { Layout, Paragraph, Section, Title } from 'components';
import { PAGES_LINKS, WORKS_DATA } from 'settings';
import { IWorkItemData } from 'types';
import css from './OneProject.module.scss';

const OneProject: FC = () => {
  const [workInfo, setWorkInfo] = useState<IWorkItemData | null>(null);
  const navigate = useNavigate();
  const { id: idLink } = useParams();

  useEffect(() => {
    if (!idLink) {
      navigate(PAGES_LINKS.notFoundPage);
      return;
    }
    const productData = WORKS_DATA.filter(({ id }) => id === idLink)[0];

    if (!productData) {
      navigate(PAGES_LINKS.notFoundPage);
      return;
    }

    setWorkInfo(productData);
  }, [idLink, navigate]);

  if (!idLink || !workInfo) return null;

  const { name, thumbnail, description, link } = workInfo;
  return (
    <Layout>
      <Section padding={true}>
        <Title className="page-title" Tag="h1">
          <span>{'<'}</span>
          {name}
          <span>{'/>'}</span>
        </Title>

        <img
          className={css.work__image}
          src={process.env.PUBLIC_URL + '/' + thumbnail}
          alt={name}
        />

        {description && <Paragraph className={css.work__text}>{description}</Paragraph>}

        <div className={css.work__buttons}>
          <Link className={css.work__buttons_backButton} to={PAGES_LINKS.projects}>
            Back
          </Link>

          {link && (
            <a className={css.work__buttons_link} href={link} target="_blank" rel="noreferrer">
              Look at work
            </a>
          )}
        </div>
      </Section>
    </Layout>
  );
};

export default OneProject;
