import { FC, useRef } from 'react';
import { useFrame, useLoader } from '@react-three/fiber';
import { Mesh, RepeatWrapping, TextureLoader } from 'three';
import { ISkillBoxItemData } from 'types';
import { AXIOS_Z_POSITION_PHOTO } from 'settings';

interface IOneSkillBoxProps extends ISkillBoxItemData {
  index: number;
}
const OneSkillBox: FC<IOneSkillBoxProps> = ({
  thumbnail,
  size,
  startPosition,
  speed,
  changePosition,
}) => {
  const ref = useRef<Mesh>(null);
  const refAngle = useRef<number>(startPosition[0]);
  const radius = 4.8;
  const colorMap = useLoader(TextureLoader, thumbnail);
  colorMap.wrapS = colorMap.wrapT = RepeatWrapping;
  colorMap.repeat.set(3, 1);

  useFrame(() => {
    if (!ref.current) {
      return;
    }

    const angle = refAngle.current;
    const sinusResult = Math.sin(angle * speed) * radius;
    const cosinusResult = Math.cos(angle * speed) * radius;

    changePosition(ref.current, sinusResult, cosinusResult);

    refAngle.current += (Math.PI / 180) * 2;
  });

  return (
    <mesh position={[...startPosition, AXIOS_Z_POSITION_PHOTO + 1]} ref={ref}>
      <sphereGeometry args={[size, 32, 32]} />
      <meshPhongMaterial map={colorMap} alphaTest={0} />
    </mesh>
  );
};

export default OneSkillBox;
