import { FC } from 'react';
import classNames from 'classnames';
import { ISkillItem } from 'types';
import css from './SkillsList.module.scss';

const elementsSkillLevel = [5, 4, 3, 2, 1];

const SkillsListItem: FC<ISkillItem> = ({ title, image, level }) => {
  return (
    <li className={css.skillsList__item} title={title}>
      {image ? (
        <img
          className={css.skillsList__item_image}
          src={process.env.PUBLIC_URL + '/' + image}
          alt={`${title}.`}
          decoding="async"
          loading="lazy"
        />
      ) : (
        <p className={css.skillsList__item_name}>{title}</p>
      )}

      <ul className={css.skillsList__item_level}>
        {elementsSkillLevel.map((item) => {
          return (
            <li
              key={item}
              className={classNames(css.levelItem, {
                [css.active]: item <= level,
              })}
            ></li>
          );
        })}
      </ul>
    </li>
  );
};

export default SkillsListItem;
