import { FC } from 'react';
import classnames from 'classnames';
import ProjectsListItem from './ProjectsListItem';
import { IWorkItemData } from 'types';
import css from './ProjectsList.module.scss';

const ProjectsList: FC<{ worksData: Array<IWorkItemData> }> = ({ worksData }) => {
  if (worksData.length === 0) return null;

  return (
    <ul className={classnames(css.projectsList, 'grid-list')}>
      {worksData.map((advantagesItemData) => (
        <ProjectsListItem key={advantagesItemData.id} {...advantagesItemData} />
      ))}
    </ul>
  );
};

export default ProjectsList;
