import { FC } from 'react';
import { IEducationItem } from 'types';
import EducationListItem from './EducationListItem';
import css from './EducationList.module.scss';

const EducationList: FC<{ listEducationData: Array<IEducationItem> }> = ({ listEducationData }) => {
  if (listEducationData.length === 0) return null;

  return (
    <ol className={css.educationList}>
      {listEducationData.map((educationItemData) => (
        <EducationListItem key={educationItemData.id} {...educationItemData} />
      ))}
    </ol>
  );
};

export default EducationList;
