import { create } from 'zustand';

export const KEY_FOR_LOCAL_STORAGE = 'l3rkjhai';

const getStartNumberCurrentPage = (): number => {
  try {
    const localStorageItem = localStorage.getItem(KEY_FOR_LOCAL_STORAGE);

    if (!localStorageItem) throw Error;

    return Number(localStorageItem);
  } catch (error) {
    return 1;
  }
};

export interface IUseStore {
  currentPage: number;
  changeACurrentPage: (page: number) => void;
}

const usePaginationStore = create<IUseStore>((set) => ({
  currentPage: getStartNumberCurrentPage(),
  changeACurrentPage: (page: number) => {
    set({ currentPage: page });
  },
}));

export default usePaginationStore;
